body {
  font-family: 100% Helvetica, sans-serif;
  background-color: #fff;
  text-align: center;
}

 .flex-container {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
          width: calc((75% - 10px) / 4);
          margin-left: 2%;
}


 img {
  width: 75%;
}


.search-form {
  background-color: teal;
  border: 1px solid;
}

.button {
  margin-left: auto;
  margin-right: auto;
}

.button.is-hidden {
  display: none;
}

.button > .spinner.is-hidden {
  display: none;
}
